import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';
i18n
    // detect user language
    // learn more: https://github.com/i18next/i18next-browser-languageDetector
    .use(LanguageDetector)
    // pass the i18n instance to react-i18next.
    .use(initReactI18next)
    // init i18next
    // for all options read: https://www.i18next.com/overview/configuration-options
    .init({
        debug: true,
        fallbackLng: 'en',
        interpolation: {
            escapeValue: false, // not needed for react as it escapes by default
        },
        resources: {
            ru: {
                translation: {
                    header: {
                        addInput: 'Новая задача...',
                        addButton: 'Добавить',
                        filters: {
                            all: 'Все задачи',
                            done: 'Выполнены',
                            active: 'Активные',
                        },
                        searchInput: 'Поиск по названию...',
                        tasksCounter: {
                            left: 'Осталось задач:',
                            empty: 'Здесь пока нет задач :(',
                        },
                    },
                    todo: {
                        changeNameInput: 'Название...',
                        buttons: {
                            change: 'Изменить',
                            delete: 'Удалить',
                            save: 'Сохранить',
                            cancel: 'Отменить',
                        },
                    }
                },
            },
            en: {
                translation: {
                    header: {
                        addInput: 'New task...',
                        addButton: 'Add',
                        filters: {
                            all: 'All Tasks',
                            done: 'Done',
                            active: 'Active',
                        },
                        searchInput: 'Task name search...',
                        tasksCounter: {
                            left: 'Tasks left:',
                            empty: 'No tasks :(',
                        }
                    },
					todo: {
                        changeNameInput: 'Task name...',
                        buttons: {
                            change: 'Change',
                            delete: 'Delete',
                            save: 'Save',
                            cancel: 'Cancel',
                        },
                    }
                },
            },
        },
    });

export default i18n;
